.hint{border:2px solid var(--light-blue-color);border-radius:8px;display:grid;grid-template-columns:24px 1fr;padding:22px 16px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;grid-gap:10px}.hint--inner{border:none;padding:10px 0}.hint__icon{width:24px;height:24px;fill:var(--light-blue-color)}.hint__text{font-weight:500;font-size:18px;line-height:21px}.hint--inner .hint__text{font-weight:400;font-size:14px;line-height:normal}