.footer{background:#e9e9e9}.footer__top{display:grid;grid-template-columns:1fr 3fr;padding:30px 50px;border-bottom:1px solid #c4c4c4}.footer__logo-wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:100%}.footer__logo{width:90px}.footer__menu{display:grid;grid-template-columns:repeat(4,1fr)}.footer__menu-title{font-weight:700;font-size:14px;color:var(--black-color);margin-bottom:25px}.footer__menu-link{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;margin-bottom:15px;color:#646464;font-size:14px}.footer__menu-link:hover{color:var(--blue-color)}.footer__menu-link-icon{width:15px;height:15px;margin-right:10px}.footer__menu-link:hover .footer__menu-link-icon{fill:var(--blue-color)}.footer__bottom{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;padding:30px 0}.footer__copyright{color:#646464;font-size:14px}