.advantages{display:grid;grid-template-columns:repeat(4,1fr);grid-gap:15px}.advantages *,.advantages :after,.advantages :before{-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}.advantages__item{padding:30px 20px;border:1px solid var(--light-gray-color);border-radius:5px;cursor:default}.advantages__item:hover{border-color:var(--blue-color);background:var(--light-gray-color)}.advantages__icon{width:56px;height:56px;display:block;stroke:var(--blue-color);fill:none;stroke-width:3.5;stroke-linecap:round;stroke-linejoin:round;z-index:2;stroke-dashoffset:0;margin-bottom:7px}.advantages__icon--1{stroke-dasharray:255.202,255.202}.advantages__item:hover .advantages__icon--1{stroke-dashoffset:255.202}.advantages__icon--2{stroke-dasharray:212.191,212.191}.advantages__item:hover .advantages__icon--2{stroke-dashoffset:212.191}.advantages__icon--3{stroke-dasharray:201.648,201.648}.advantages__item:hover .advantages__icon--3{stroke-dashoffset:201.648}.advantages__icon--4{stroke-dasharray:263.599,263.599}.advantages__item:hover .advantages__icon--4{stroke-dashoffset:263.599}.advantages__item-title{font-weight:700;font-size:20px;color:var(--black-color);margin-bottom:5px;-webkit-transform-origin:left bottom;-ms-transform-origin:left bottom;transform-origin:left bottom}.advantages__item:hover .advantages__item-title{-webkit-transform:scale(1.2);-ms-transform:scale(1.2);transform:scale(1.2);color:var(--blue-color)}.advantages__item-desc{font-size:16px;color:var(--gray-color)}.advantages__item:hover .advantages__item-desc{color:var(--blue-color)}