.photo-upload__items{display:grid;grid-template-columns:repeat(4,1fr);grid-gap:10px}.product-in-tender .photo-upload__items{grid-template-columns:repeat(3,1fr)}.photo-upload__items--six{grid-template-columns:repeat(6,1fr)}.photo-upload__label-wrapper{width:100%}.photo-upload__label{padding-bottom:100%;position:relative;border:1px dashed var(--gray-color);border-radius:8px;cursor:pointer}.photo-upload__label:hover{background:var(--super-light-blue-color)}.photo-upload__input-text{position:absolute;left:50%;bottom:20%;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%);text-align:center;color:var(--blue-color)}.product-in-tender .photo-upload__input-text{bottom:15%}.photo-upload__input-icon-wrapper{width:20px;height:20px;position:absolute;left:50%;top:20%;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%)}.product-in-tender .photo-upload__input-icon-wrapper{top:15%}.photo-upload__input-icon{width:100%;height:100%;display:block;fill:var(--blue-color)}.photo-upload__input{display:none}.photo-upload__preview-item,.photo-upload__preview-wrapper{width:100%;position:relative}.photo-upload__preview-item{padding-bottom:100%}.photo-upload__preview{display:block;position:absolute;width:100%;height:100%;top:0;left:0;border-radius:8px;-o-object-fit:cover;object-fit:cover;-o-object-position:center;object-position:center}.photo-upload__preview-star{width:30px;height:30px;position:absolute;left:8px;top:8px;background:var(--white-color);border-radius:50%;z-index:1;cursor:pointer;padding:5px}.photo-upload__preview-star-img{width:100%;height:100%;fill:var(--gray-color)}.photo-upload__preview-star--active .photo-upload__preview-star-img,.photo-upload__preview-star:hover .photo-upload__preview-star-img{fill:var(--blue-color)}