.categories-preview{display:grid;grid-template-columns:repeat(8,1fr);grid-gap:15px}.categories-preview__item-img{width:140px;height:140px;-o-object-fit:cover;object-fit:cover;-o-object-position:center center;object-position:center center;margin-bottom:10px;border-radius:5px}.categories-preview__item-title{font-size:16px;line-height:18px;text-align:center;color:var(--black-color)}.categories-preview__item:hover .categories-preview__item-title{color:var(--blue-color)}