.product-in-tender__wrapper{max-height:calc(100vh - 300px);border-top:1px solid var(--light-gray-color);border-bottom:1px solid var(--light-gray-color);overflow-y:auto;padding:10px;overflow:auto}.product-in-tender__wrapper::-webkit-scrollbar-button{background-image:none;background-repeat:no-repeat;width:4px;height:0}.product-in-tender__wrapper::-webkit-scrollbar-track{background:var(--light-gray-color);cursor:pointer}.product-in-tender__wrapper::-webkit-scrollbar-thumb{background-color:var(--blue-color);cursor:pointer}.product-in-tender__wrapper::-webkit-scrollbar-thumb:hover{background-color:var(--dark-blue-color);cursor:pointer}.product-in-tender__wrapper::-webkit-resizer{background-image:none;background-repeat:no-repeat;width:4px;height:0}.product-in-tender__wrapper::-webkit-scrollbar{width:4px}.product-in-tender__item{color:var(--black-color);padding-bottom:20px;border-bottom:1px solid var(--light-gray-color);margin-bottom:20px}.product-in-tender__item:last-of-type{border-bottom:none;margin-bottom:0}.product-in-tender__item-header{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-bottom:10px}.product-in-tender__item-header,.product-in-tender__item-header-title{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.product-in-tender__item-header-title{font-size:24px;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.product-in-tender__item-header-title-number{margin-left:5px}.product-in-tender__item-header-delete{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:pointer}.product-in-tender__item-header-delete-text{color:var(--gray-color);font-weight:500;font-size:16px}.product-in-tender__item-header-delete:hover .product-in-tender__item-header-delete-text{color:var(--blue-color)}.product-in-tender__item-header-delete-icon{margin-left:5px;width:20px;height:20px;fill:var(--gray-color);display:block}.product-in-tender__item-header-delete:hover .product-in-tender__item-header-delete-icon{fill:var(--blue-color)}.product-in-tender__item-inputs{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-areas:"name count comment" "images example comment";grid-gap:15px}.product-in-tender__item-input-name{grid-area:name}.product-in-tender__item-input-price{grid-area:price}.product-in-tender__item-input-comment{grid-area:comment}.product-in-tender__item-input-count{grid-area:count;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start}.product-in-tender__item-input-count-title{font-size:17px;color:var(--gray-color);margin-right:5px}.product-in-tender__item-input-example{grid-area:example;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;position:relative;z-index:100}.product-in-tender__item-input-example-tooltip{margin-left:10px}.product-in-tender__item-input-images{grid-area:images}.product-in-tender__add-product{margin-left:auto;margin-right:auto;margin-bottom:10px}.product-in-tender__footer{padding-top:20px}