.counter{display:grid;grid-template-columns:33px 60px 33px;grid-gap:5px}.counter__button{width:33px;height:33px;background:var(--blue-color);position:relative;border-radius:50%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.counter__button:after{content:"";position:absolute;display:block;width:100%;height:100%;top:0;left:0;background:var(--middle-blue-color);z-index:-1;-webkit-transform:scale(.9);-ms-transform:scale(.9);transform:scale(.9);border-radius:50%}.counter__button:active:after{-webkit-transform:scaleX(1.1) scaleY(1.3);-ms-transform:scaleX(1.1) scaleY(1.3);transform:scaleX(1.1) scaleY(1.3);opacity:0}.counter__button-icon{width:15px;height:15px;fill:var(--white-color)}.counter__input{border-radius:8px;text-align:center;background:var(--extra-light-blue-color);color:var(--black-color);font-size:16px;padding:0}