.min-max-slider__inputs{display:grid;grid-template-columns:1fr 1fr;grid-gap:14px}.min-max-slider__range{height:3px;border:none;-webkit-box-shadow:none;box-shadow:none;background:var(--extra-light-blue-color);margin:15px auto;width:calc(100% - 16px)}.min-max-slider__range .noUi-connect{background:var(--light-blue-color)}.min-max-slider__range .noUi-handle{width:16px;height:16px;border-radius:50%;background:var(--light-blue-color);-webkit-box-shadow:none;box-shadow:none;right:-9px;border:none}.min-max-slider__range .noUi-handle:after,.min-max-slider__range .noUi-handle:before{display:none}.min-max-slider__range *,.min-max-slider__range :after,.min-max-slider__range :before{-webkit-transition:none;-o-transition:none;transition:none}