.chat{border-radius:8px;padding:15px;margin-top:15px}.chat__title{text-align:center;color:var(--gray-color);font-size:13px}.chat__messages{display:grid;grid-gap:10px}.chat__date{margin:15px 0;text-align:center;color:var(--gray-color)}.chat__time{text-align:right}.chat__message{display:grid;grid-template-columns:1fr 50px;grid-gap:20px}.chat__message-content{padding:15px;background:var(--extra-light-blue-color);border-radius:8px;width:calc(100% - 50px);margin-left:auto}.chat__message--invert .chat__message-content{background:var(--light-gray-color);margin-left:0}.chat__message-content-text{color:var(--black-color);font-size:15px}.chat__message-content-images{margin-top:10px;display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap}.chat__message-content-image{display:block;width:auto;height:200px;border-radius:5px;border:1px solid var(--light-blue-color);margin-right:10px;margin-bottom:10px}.chat__message--invert .chat__message-content-image{border-color:var(--gray-color)}.chat__message-content-files{margin-top:10px}.chat__message-content-file{display:grid;grid-template-columns:20px 1fr;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;grid-gap:10px}.chat__message-content-file-icon{width:100%;height:30px;fill:var(--blue-color)}.chat__message-content-file-text{color:var(--blue-color);font-size:16px}.chat__message-content-file:hover .chat__message-content-file-text{text-decoration:underline}.chat__form{background:var(--extra-light-blue-color);border-radius:5px;margin-top:15px;border:1px var(--blue-color);border-style:dashed;padding-bottom:15px}.chat__form-buttons{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end;padding:0 15px}.chat__form-buttons,.chat__form-file-input-label{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.chat__form-file-input-label{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:48px;padding:0 20px;cursor:pointer;border:2px var(--blue-color);border-style:dashed;border-radius:5px}.chat__form-file-input-label:hover{border-style:solid}.chat__form-file-input-text{color:var(--blue-color);font-weight:500;font-size:16px}.chat__form-file-input-icon{width:20px;height:20px;fill:var(--blue-color);margin-left:5px}.chat__form-file-preview{padding:0 15px}.chat__form-file-preview--empty{display:none}.chat__form-send{margin-left:20px}.chat__form-file-input{display:none}.chat__form-wrapper-input{width:calc(100% - 15px);height:100px;min-height:100px;font-size:16px;line-height:20px;padding:15px 0 15px 15px;-webkit-transition:none;-o-transition:none;transition:none;resize:vertical}.chat__form-wrapper-input::-webkit-input-placeholder{color:var(--light-blue-color)}.chat__form-wrapper-input:-moz-placeholder,.chat__form-wrapper-input::-moz-placeholder{color:var(--light-blue-color)}.chat__form-wrapper-input:-ms-input-placeholder{color:var(--light-blue-color)}