.banner{position:relative}.banner,.banner__img{width:100%;height:100%;display:block}.banner__img{position:absolute;top:0;left:0;-o-object-fit:cover;object-fit:cover;-o-object-position:center center;object-position:center center}.banner__desc{position:absolute;z-index:2;background:var(--middle-green-color);padding:30px 20px;color:var(--white-color);left:0;bottom:50px}.banner__title{font-weight:500;font-size:22px;line-height:28px;margin-bottom:10px}.banner__text{font-size:16px;line-height:18px}