.main-banner{height:219px;position:relative;overflow:hidden;border-radius:5px;display:block}.main-banner__background{background:var(--light-blue-color)}.main-banner__background,.main-banner__background-el{position:absolute;width:100%;height:100%;top:0;left:0}.main-banner__background-el{-webkit-animation:blink 4s linear;animation:blink 4s linear;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}.main-banner__background-el--1{width:506px;background:url(/storage/images/components-assets/main-banner/img/main-banner-1.svg);background-repeat:no-repeat;-webkit-animation-delay:.6s;animation-delay:.6s}.main-banner__background-el--2{width:750px;background:url(/storage/images/components-assets/main-banner/img/main-banner-2.svg);background-repeat:no-repeat;left:480px;-webkit-animation-delay:1.2s;animation-delay:1.2s}.main-banner__background-el--3{width:955px;background:url(/storage/images/components-assets/main-banner/img/main-banner-3.svg);background-repeat:no-repeat;-webkit-animation-delay:1.8s;animation-delay:1.8s}.main-banner__background-el--4{width:1019px;background:url(/storage/images/components-assets/main-banner/img/main-banner-4.svg);background-repeat:no-repeat;left:175px;-webkit-animation-delay:2.4s;animation-delay:2.4s}.main-banner__background-el--5{width:728px;background:url(/storage/images/components-assets/main-banner/img/main-banner-5.svg);background-repeat:no-repeat;-webkit-animation-delay:3s;animation-delay:3s}.main-banner__background-el--6{width:865px;background:url(/storage/images/components-assets/main-banner/img/main-banner-6.svg);background-repeat:no-repeat;left:365px;-webkit-animation-delay:3.6s;animation-delay:3.6s}@-webkit-keyframes blink{0%{opacity:1}50%{opacity:0}to{opacity:1}}@keyframes blink{0%{opacity:1}50%{opacity:0}to{opacity:1}}.main-banner__content{position:relative;z-index:3;display:grid;grid-template-columns:2fr 1.35fr;padding:23px 50px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:100%}.main-banner__content--ref{grid-template-columns:1fr 1fr}.main-banner__content:after{content:"";position:absolute;display:block;width:1px;height:calc(100% - 50px);left:59%;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);background:var(--light-blue-color)}.main-banner__content--ref:after{left:45%}.main-banner__content-title{font-weight:700;font-size:40px;color:var(--white-color);margin-bottom:10px}.main-banner__content-subtitle{color:var(--dark-blue-color);font-size:20px;font-weight:500}.main-banner__content-subtitle-small{display:inline}.main-banner__content-subtitle-big{display:inline;font-size:33px}.main-banner__content-natural{padding-left:25px;display:grid;grid-gap:30px}.main-banner__content-natural-item{display:grid;grid-template-columns:45px 1fr;grid-gap:15px;margin-bottom:10px;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.main-banner__content-natural-item-icon{width:100%;height:50px;fill:var(--white-color)}.main-banner__content-natural-item-percent{font-size:30px;font-weight:700;color:var(--white-color)}.main-banner__content-natural-item-text{font-size:16px;font-weight:500;color:var(--white-color);text-transform:uppercase}