.tooltip{position:relative}.tooltip__button{cursor:pointer;width:20px;height:20px;fill:var(--blue-color)}.tooltip__inner{position:absolute;left:calc(100% + 10px);top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);width:300px;opacity:0;visibility:hidden;-webkit-transition-delay:.1s;-o-transition-delay:.1s;transition-delay:.1s}.tooltip--active .tooltip__inner{opacity:1;visibility:visible}.tooltip--right .tooltip__inner{left:auto;right:0}.tooltip--fill .tooltip__inner{width:100%}.tooltip__close{display:none}.tooltip__content{background:var(--white-color);border:1px solid var(--gray-color);padding:10px;border-radius:5px}