.main-navigation__content{position:relative;z-index:3;display:grid;grid-template-columns:2fr 1.27fr;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:100%}.main-navigation__content:after{content:"";position:absolute;display:block;width:1px;height:calc(100% + 10px);left:59%;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);background:var(--gray-color);opacity:.5}.main-navigation__title{font-weight:700;font-size:24px;margin-bottom:5px}.main-navigation__desc{font-size:16px;margin-bottom:25px}.main-navigation__button{border-radius:5px;border:1px solid var(--blue-color);background:var(--blue-color);color:var(--white-color);font-weight:500;font-size:16px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;text-align:center;width:290px;height:56px;cursor:pointer;position:relative;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.main-navigation__button:after{content:"";position:absolute;display:block;width:100%;height:100%;top:0;left:0;background:var(--middle-blue-color);z-index:-1;-webkit-transform:scale(.9);-ms-transform:scale(.9);transform:scale(.9);border-radius:4px}.main-navigation__button:active:after{-webkit-transform:scaleX(1.1) scaleY(1.3);-ms-transform:scaleX(1.1) scaleY(1.3);transform:scaleX(1.1) scaleY(1.3);opacity:0}.main-navigation__button--invert,.main-navigation__button:hover{background:var(--white-color);color:var(--blue-color)}.main-navigation__button--invert:hover{background:var(--blue-color);color:var(--white-color)}