.tender-header{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.tender-header,.tender-header__main-title{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row}.tender-header__main-title{font-size:24px;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start}.tender-header__main-title-name{color:var(--black-color);margin-right:10px}.tender-header__main-title-number{color:var(--gray-color);margin-right:10px}.tender-header__main-title-status{margin-right:10px;margin-left:20px}.tender-header__main-title-status-title{font-size:13px;margin-bottom:4px;text-align:center;padding-right:4px}.tender-header__main-title-status-title--green{color:var(--middle-green-color)}.tender-header__main-title-status-title--gold{color:var(--gold-color)}.tender-header__main-title-status-title--gray{color:var(--gray-color)}.tender-header__main-title-status-line{width:120px;height:4px}.tender-header__main-assessment{margin-right:10px}.tender-header__main-buttons{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;margin-top:20px}.tender-header__main-button{margin-right:20px}.tender-header__desc-item{display:grid;grid-template-columns:1fr -webkit-max-content;grid-template-columns:1fr max-content;grid-gap:5px}.tender-header__desc-item-name{text-align:right;color:var(--gray-color)}.tender-header__desc-item-value{color:var(--black-color)}