.checkbox{position:relative;padding-left:30px;cursor:pointer;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;min-height:24px}.checkbox__input{display:none}.checkbox__icon{position:absolute;width:20px;height:20px;fill:var(--blue-color);left:2px;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);opacity:0}.checkbox__input:checked+.checkbox__item .checkbox__icon{opacity:1}.checkbox__text{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.checkbox__text:after{content:"";position:absolute;display:block;left:0;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);width:24px;height:24px;border:1px solid var(--gray-color);border-radius:2px}