.manufacturer-preview{padding:18px 13px;display:grid;grid-template-columns:156px 1fr;grid-gap:18px}.manufacturer-preview__img{width:100%;height:156px;-o-object-fit:contain;object-fit:contain;-o-object-position:center;object-position:center}.manufacturer-preview__desc{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;padding:12px 0}.manufacturer-preview__desc-title{font-weight:500;font-size:20px;margin-bottom:8px;margin-top:-5px}.manufacturer-preview__assessment{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.manufacturer-preview__assessment-value{font-size:16px;margin-right:5px}.manufacturer-preview__assessment--empty .manufacturer-preview__assessment-value{color:var(--gray-color)}.manufacturer-preview__assessment-star{width:18px;height:18px;fill:var(--gold-color)}.manufacturer-preview__assessment--empty .manufacturer-preview__assessment-star{fill:var(--gray-color)}.manufacturer-preview__desc-counters{margin-top:auto}.manufacturer-preview__desc-counter{margin-top:5px;font-size:16px}.manufacturer-preview__desc-counter-number{display:inline;color:var(--black-color)}.manufacturer-preview__desc-counter-text{display:inline;color:var(--gray-color)}