.reviews{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.reviews__desc{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-bottom:12px}.reviews__desc,.reviews__item{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.reviews__item{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;grid-area:item}.reviews__item-value{font-weight:500;font-size:20px;margin-right:8px;color:var(--black-color)}.reviews__item-star{width:22px;height:22px;display:block;fill:var(--gold-color)}.reviews__count{grid-area:count;text-align:right;font-size:14px;color:var(--gray-color)}.reviews__button{grid-area:button;height:40px}