.selected-products{display:grid;grid-template-columns:1fr 1fr;grid-gap:15px}.selected-products__block{padding:20px}.selected-products__title{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;margin-bottom:15px}.selected-products__title-text{font-size:24px;font-weight:700}.selected-products__title-icon{display:block;width:24px;height:24px;margin-left:10px}.selected-products__title-icon--star{fill:#ffb72a}.selected-products__title-icon--new-products{fill:#1eca82}.selected-products__items{display:grid;grid-template-columns:repeat(3,1fr);grid-gap:15px}.selected-products__item-img{width:156px;height:156px;-o-object-fit:contain;object-fit:contain;-o-object-position:center;object-position:center}.selected-products__item-price{font-weight:700;font-size:18px;color:var(--black-color);margin-bottom:3px}.selected-products__minimal{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:baseline;-ms-flex-align:baseline;align-items:baseline;font-size:12px;margin-bottom:7px}.selected-products__minimal-count{font-weight:500;color:var(--black-color)}.selected-products__minimal-desc{color:var(--gray-color);margin-left:4px}