.breadcrumbs{margin-bottom:18px}.breadcrumbs,.breadcrumbs__item{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start}.breadcrumbs__item{font-size:12px;color:var(--gray-color);-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:pointer}.breadcrumbs__item:after{content:"";border:solid var(--gray-color);border-width:0 1px 1px 0;display:block;margin:0 10px;width:6px;height:6px;-webkit-transform:rotate(-45deg);-ms-transform:rotate(-45deg);transform:rotate(-45deg)}.breadcrumbs__item:hover{color:var(--blue-color)}.breadcrumbs__item--active{color:var(--black-color);cursor:default}.breadcrumbs__item--active:after{display:none}.breadcrumbs__item--active:hover{color:var(--black-color)}