.tender-row{border-bottom:1px solid var(--light-gray-color);display:grid;grid-template-columns:80px 5fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-gap:10px;padding-left:10px;padding-right:10px;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.tender-row--without-assessment{grid-template-columns:80px 6fr 1fr 1fr 1fr 1fr 1fr}.tender-row--product{grid-template-columns:80px 5fr 1fr 1fr 2fr}.tender-row--offer{grid-template-columns:80px 3fr 1fr 1fr 1fr 1fr 2fr}.tender-row--offer-with-manufacturer{grid-template-columns:80px 4fr 2fr 2fr 2fr 2fr 2fr 1fr}.tender-row--selected{background:var(--super-light-blue-color)}.tender-row__item{padding:8px 0;text-align:center;font-size:16px;color:var(--black-color)}.tender-row__item:first-of-type{padding-left:0}.tender-row__item:nth-of-type(2){text-align:left}.tender-row__item:last-of-type{padding-right:0}.tender-row--header .tender-row__item{font-size:13px;font-weight:700}.tender-row__item--small{font-size:14px;color:var(--gray-color)}.tender-row__item--link:hover{color:var(--blue-color)}.tender-row__item--middle{font-size:18px}.tender-row__item--big{font-size:18px;font-weight:500}.tender-row__item--left{text-align:left}.tender-row__item--right{text-align:right}.tender-row__item--center{text-align:center}.tender-row__preview{width:80px;height:80px;-o-object-fit:contain;object-fit:contain;-o-object-position:center;object-position:center;position:relative}.tender-row__preview--empty{background:var(--extra-light-blue-color)}.tender-row__preview--logo{border-radius:50%}.tender-row__preview:after{display:block;position:absolute;content:attr(data-name);width:100%;height:100%;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:55px;font-weight:700;color:var(--blue-color)}.tender-row__preview--zoom{cursor:pointer;position:relative}.tender-row__preview-zoom-text{position:absolute;left:0;bottom:6px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:3px 8px;background:var(--blue-color);color:var(--white-color);font-size:11px;border-radius:4px}.tender-row__preview-img{width:80px;height:80px;-o-object-fit:contain;object-fit:contain;-o-object-position:center;object-position:center}.tender-row__status-title{font-size:14px;color:var(--gray-color);margin-bottom:8px}.tender-row__status-line{height:4px;width:calc(100% - 4px);margin-left:4px}.tender-row__item-assessment{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end}.tender-row__item-assessment--empty{position:relative}.tender-row__item-assessment--empty:after{content:"";position:absolute;display:block;right:0;top:calc(50% - 1px);width:24px;height:2px;background:var(--light-gray-color)}.tender-row__item-assessment-value{font-size:18px;font-weight:500;margin-right:5px}.tender-row__item-assessment-star{width:18px;height:18px;fill:var(--gold-color)}.tender-row__item-icon{width:16px;height:16px}.tender-row__item-icon--check{fill:var(--green-color)}.tender-row__item-icon--not-check{fill:var(--red-color)}.tender-row__item-options{display:grid;grid-gap:7px;margin:7px 0}.tender-row__item-title{font-size:12px;color:var(--gray-color);margin-bottom:-2px}.tender-row__item-text{font-size:14px}.tender-row__item-text--bold{font-size:16px;font-weight:700}