.buyer{position:relative;display:grid;grid-template-columns:114px 1fr 250px;padding-right:25px;grid-gap:0 15px;grid-template-areas:"logo title reviews" "logo options reviews";-webkit-box-align:center;-ms-flex-align:center;align-items:center}.buyer:after{content:"";width:calc(100% - 128px);height:100%;right:0;top:0;background:var(--light-gray-color);border-radius:8px;z-index:-1}.buyer:after,.buyer__logo{position:absolute;display:block}.buyer__logo{width:114px;height:114px;border-radius:50%;-o-object-fit:cover;object-fit:cover;-o-object-position:center;object-position:center;grid-area:logo;background:var(--extra-light-blue-color)}.buyer__logo:after{display:block;position:absolute;content:attr(data-name);width:100%;height:100%;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:55px;font-weight:700;color:var(--blue-color)}.buyer__title{grid-area:title;font-weight:700;font-size:24px;padding:16px 24px 0}.buyer__options{grid-area:options;padding:12px 24px;display:grid;grid-template-columns:repeat(4,1fr);grid-gap:15px}.buyer__option{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:baseline;-ms-flex-align:baseline;align-items:baseline}.buyer__option-name{font-size:14px;color:var(--gray-color)}.buyer__option-value{font-weight:500;font-size:18px;color:var(--black-color);margin-left:5px}.buyer__reviews{grid-area:reviews;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}