.logo{width:100%;position:relative}.logo__img{width:100%;height:auto;display:block}.logo__line{position:absolute;background:var(--blue-color);left:0;top:51%;width:79%;height:10%;-webkit-transform-origin:top;-ms-transform-origin:top;transform-origin:top;-webkit-transform:scaleY(1);-ms-transform:scaleY(1);transform:scaleY(1)}.logo:hover .logo__line{-webkit-animation:logo-scale .3s ease-in;animation:logo-scale .3s ease-in}@-webkit-keyframes logo-scale{0%{-webkit-transform:scaleY(1);transform:scaleY(1);background:var(--blue-color)}50%{-webkit-transform:scaleY(.5);transform:scaleY(.5);background:var(--black-color)}to{-webkit-transform:scaleY(1);transform:scaleY(1);background:var(--blue-color)}}@keyframes logo-scale{0%{-webkit-transform:scaleY(1);transform:scaleY(1);background:var(--blue-color)}50%{-webkit-transform:scaleY(.5);transform:scaleY(.5);background:var(--black-color)}to{-webkit-transform:scaleY(1);transform:scaleY(1);background:var(--blue-color)}}.logo__text{color:var(--gray-color);font-size:12px;font-weight:500;white-space:nowrap;margin-top:6%;margin-bottom:-10%}.logo:hover .logo__text{color:var(--black-color)}